$base-white: #f6f2ef;
$base-blue: blue;
$base-grey: #dddcd8;
$base-yellow: #e5a100;

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  margin: 0;
  font-family: "Nunito", Arial;
  background-color: $base-white;
}

section {
  height: 100vh;
  padding: 10em;

  h1 {
    font-size: 3.5em;
  }
}

#home {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  nav {
    margin-top: 2em;
  }

  a {
    color: black;
    margin: 2em;
    text-decoration: none;
  }

  a:hover {
    color: $base-blue;
  }

  .warning {
    margin-top: 3em;
    color: $base-yellow;
  }
}

#about {
  background-color: $base-grey;

  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
  }
}
